* {
  margin: 0;
  padding: 0;
}
body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.active {
  border-bottom: 1.5px solid #969696;
}
.app {
  width: 320px;
  background-color: #eacc21;
  margin: 5px auto;
  border: 1px solid #969696;
  border-radius: 5px;
}
nav {
  margin: 0 auto;
  width: 300px;
  display: flex;
  justify-content: space-around;
  border-bottom: 1px solid #969696;
  margin-bottom: 10px;
}
nav a {
  text-decoration: none;
  padding: 5px;
  margin-bottom: 5px;
  color: black;
}
.place-container {
  border-bottom: 1px solid #969696;
  margin: 0 auto 15px;
  width: 300px;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}
li {
  padding: 5px;
  width: 250px;
  display: flex;
  justify-content: space-around;
  flex-direction: column;
  align-items: center;
  border: 1px solid #969696;
  border-radius: 5px;
  margin-bottom: 5px;
  box-shadow: inset 0px 0px 20px 1px rgba(0, 0, 0, 0.6);
}
li div {
  text-align: center;
  width: 150px;
  overflow-x: hidden;
  text-overflow: ellipsis;
  border-bottom: 1px solid #969696;
}
button {
  border-radius: 5px;
  border: 1px solid #969696;
  padding: 5px 10px;
  margin: 5px;
}
button:hover {
  background-color: #ffffff;
}
.del-btn {
  border-radius: 15px;
  border: 1px solid #969696;
  font-size: 14px;
  padding: 5px;
  margin: 10px 0;
}
li a {
  text-align: center;
  width: 150px;
  text-decoration: none;
  border-bottom: 1px solid #969696;
}
form {
  display: flex;
  flex-direction: column;
  padding: 10px;
}
input,
label {
  padding: 5px;
  border-radius: 5px;
}
input {
  border: 1px solid #969696;
}
input[type="submit"] {
  margin: 10px 0;
}
input[type="submit"]:hover {
  background-color: #ffffff;
}
